<template>
  <div class="app-container">
    <el-scrollbar height="800px">
    <el-table  class="table-content"
        :data="dataList"
        border
        style="width: 100%;"
    >
      <el-table-column
      label="序号"
      fixed="left"
      width="50"          
      align="center"
      color="black"
      >
      <template v-slot="scope">
          {{ scope.$index+1 }}
      </template>
      </el-table-column>

      <el-table-column
      label="学生学号"
      align="center"
      color="black"
      sortable
      prop="userNum" />

      <el-table-column
      label="学生姓名"
      align="center"
      color="black"
      prop="userName" />

      <el-table-column
      label="课程编号"
      align="center"
      color="black"
      sortable
      prop="courseNum" />

      <el-table-column
      label="课程名称"
      align="center"
      color="black"
      prop="courseName" />

      <el-table-column
      label="课程教师"
      align="center"
      color="black"
      sortable
      prop="courseteacher" />

      <el-table-column
      label="课程学分"
      align="center"
      color="black"
      sortable
      prop="coursescore" />

      <el-table-column
      label="成绩"
      align="center"
      color="black"
      sortable
      prop="courseselectmark" />

      <el-table-column
          label="操作"
          align="center"
          color="black"
      >
      <template v-slot="scope">
        <el-button type="primary" size="mini" @click="givemarkready(scope.row.courseNum,scope.row.courseName,scope.row.userNum,scope.row.tempmark)">打分</el-button>
      </template>
      </el-table-column>
    </el-table>
    </el-scrollbar>
  </div>

  <el-dialog title="打分" v-model="markVisible" width="30%">
    <el-form  label-width="70px">
      <el-form-item label="课程编号">
        <el-input type='text' v-model="form.courseNum" :disabled="true"></el-input>
      </el-form-item>
      <el-form-item label="课程名称">
        <el-input type='text' v-model="form.courseName" :disabled="true"></el-input>
      </el-form-item>
      <el-form-item label="学生学号">
        <el-input type='text' v-model="form.userNum" :disabled="true"></el-input>
      </el-form-item>
      <el-form-item label="成绩">
        <el-input type='text' v-model="form.tempmark"></el-input>
      </el-form-item>
    </el-form>
    <span  class="dialog-footer">
        <el-button @click="markVisible = false">取 消</el-button>
        <el-button type="primary" @click="givetempmark(this.form.courseNum,this.form.userNum,this.form.tempmark)">暂 存</el-button>
        <el-button type="primary" @click="givemark(this.form.courseNum,this.form.userNum,this.form.tempmark)">录 入</el-button>
    </span>
  </el-dialog>
</template>

<script>
import { ElMessage } from 'element-plus'
import {getallselectcourseList,givemark,givetempmark} from '@/service/genServ.js'
export default {
  name: 'givemark',
  data() {
    return {
      form: {
        courseNum:'',
        courseName:'',
        userNum:'',
        tempmark:'',
        courseselectmark:'',
      },
      markVisible : false,
      dataList:[],
    }
  },
  created() {
    this.doQuery();
  },
  methods: {
    doQuery() {
       getallselectcourseList().then(res => {
        this.dataList = res.data;
      })
    },

    givemarkready(courseNum,courseName,userNum,tempmark) {
      this.markVisible = true;
      this.form.courseNum=courseNum;
      this.form.courseName=courseName;
      this.form.userNum=userNum;
      this.form.tempmark=tempmark;
    },

    givetempmark(courseNum,userNum,tempmark) {
       givetempmark({'courseNum':courseNum,'userNum':userNum,'tempmark':tempmark}).then(res => {
        console.log(res)
         ElMessage({
          message: '成绩暂存成功！',
          type: 'success',
        })
        })
        this.markVisible = false;
        this.doQuery();
      },
    
  
    givemark(courseNum,userNum,tempmark) {
       givemark({'courseNum':courseNum,'userNum':userNum,'tempmark':tempmark}).then(res => {
        console.log(res)
        ElMessage({
          message: '成绩录入成功！',
          type: 'success',
        })
        this.markVisible = false;
        this.doQuery();
      })
    },

    },

}
</script>

<style scoped>
.app-container{
  width: 1650px;
  position:absolute;
  top:100px;
  left:225px;
}
</style>
